
import {mapState,mapActions} from 'vuex'
import CardList from './card-list'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

const list_type=['small','medium','large'] //small--, medium--  large --
export default {
  name:'list',
  data:function(){
    return {
      demo4Value: {
        pullupStatus: 'default'
      },
      loading:false,
      type:'medium',
      search_input:'',
      pullupConfig:{
        content: '',
        downContent: '',
        upContent: '',
        loadingContent: '...'
      },
    }
  },
  components:{
    Flexbox,
    FlexboxItem,
    Search,
    CardList,
    Scroller,
    Spinner
  },
  created:function(){
    //console.log($(".page-list .weui_search_bar:before"));
      this.GETGOODSLIST();
  },
  computed:mapState({
    goods_data:state=>state.goods.goods_list.data,
    current_page:state=>state.goods.goods_list.current_page,
    last_page:state=>state.goods.goods_list.last_page,
    total:state=>state.goods.goods_list.total,
    from:state=>state.goods.goods_list.from,
    to:state=>state.goods.goods_list.to,
    per_page:state=>state.goods.goods_list.per_page
  }),
  methods:{
    ...mapActions(['GETGOODSLIST']),

    toggleType:function(){
      //console.log("");
      this.type=this.type=='medium'?'large':'medium';
    },
    load:function(){
      console.log("");
      setTimeout(()=>{
        this.list_data.push({
          url:'/goods',
          name:'YINER2016/86319660',
          img:'http://mall.yingerfashion.com/public/images/cd/6a/4b/1a799542c9d0a4919d14dba60da2e2246d799cbe.jpg',
          title:'',
          price:'276.00',
          mktprice:'1380.00'
        },
        {
          url:'/goods',
          name:'YINER2016/86319660',
          img:'http://mall.yingerfashion.com/public/images/cd/6a/4b/1a799542c9d0a4919d14dba60da2e2246d799cbe.jpg',
          title:'',
          price:'276.00',
          mktprice:'1380.00'
        })
        setTimeout(()=>{
          this.$refs.listScroll.donePullup();
        },100)

      },2000)
    }
  }

}



